import React, { Fragment } from 'react'
import { Link, StaticQuery, graphql } from 'gatsby'
import logo from '../img/logo.svg'
import '../scss/chumba.scss'

const NavbarWrapper = ({ data }) => {
  //console.log(data);
  return (
    <Fragment>
      <div className="navpad" />
      <nav
        className="navbar navbar-expand-lg bg-secondary fixed-top text-uppercase"
        id="mainNav"
      >
        <div className="container">
          <Link to="/" className="navbar__home-item">
            <figure className="image">
              <img
                src={logo}
                className="responsive navbar-logo"
                alt="Chumba Casino"
                style={{ width: 88 }}
              />
            </figure>
          </Link>
          <div className="navbar__right">
            <a
              id="btn_signup_mobile"
              className="navbar__btn d-block d-lg-none btn btn-yellow-grad trackingLink"
              href="https://login.chumbacasino.com/register"
            >
              Sign Up
            </a>
            <a
              id="btn_login_mobile"
              className="navbar__btn d-block d-lg-none btn btn-yellow trackingLink"
              href="https://login.chumbacasino.com"
            >
              Login
            </a>
            <button
              className="navbar-toggler navbar-toggler-right text-uppercase text-white rounded"
              type="button"
              data-toggle="collapse"
              data-target="#navbarResponsive"
              aria-controls="navbarResponsive"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="screen-reader-text">Menu</span>
              <i className="fa fa-bars" />
            </button>
          </div>

          <div className="collapse navbar-collapse" id="navbarResponsive">
            <ul className="navbar-nav ml-auto">
              {data.allWordpressWpApiMenusMenusItems.edges[0].node.items.map(
                item => (
                  <li
                    key={item.object_slug}
                    className="nav-item mx-0 mx-lg-1 dropdown"
                  >
                    <Link
                      className="nav-link py-3 px-0 px-lg-3 rounded "
                      to={item.url.replace('http://localhost:8000', '')}
                      key={item.object_slug}
                    >
                      {item.title}
                    </Link>

                    {item.wordpress_children && (
                      <ul
                        className="dropdown-menu"
                        role="menu"
                        aria-labelledby="dLabel"
                      >
                        {item.wordpress_children &&
                          item.wordpress_children.map(subitem => (
                            <li key={subitem.wordpress_id}>
                              <Link
                                to={subitem.url.replace(
                                  'http://localhost:8000',
                                  ''
                                )}
                              >
                                {subitem.title}
                              </Link>
                            </li>
                          ))}
                      </ul>
                    )}
                  </li>
                )
              )}
            </ul>
          </div>
        </div>
      </nav>
    </Fragment>
  )
}

export default () => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressWpApiMenusMenusItems(
          filter: { slug: { eq: "chumba-top-menu" } }
        ) {
          edges {
            node {
              name
              count
              items {
                order
                title
                url
                object_slug
                wordpress_children {
                  wordpress_id
                  title
                  url
                  object_slug
                }
              }
            }
          }
        }
      }
    `}
    render={data => <NavbarWrapper data={data} />}
  />
)
